  align-items: center;
  margin: 0 auto;
  padding: 15px 0;
  display: flex;
}

.logo-section {
  color: var(--black-color);
  flex-direction: row;
  align-items: center;
  font-size: 40px;
  display: flex;
}

.site-logo {
  width: 70px;
  height: 70px;
  border-radius: 75px;
  margin-right: 10px;
}

.nav-list {
  flex-direction: row;
  display: flex;
}

.nav-list__button {
  color: var(--black-color);
  background-color: rgba(0, 0, 0, 0);
  font-size: 20px;
}

.nav-list__item:not(:last-child) {
  margin-right: 30px;
}

.header__login-btns-container {
  width: 70px;
