  background-color: rgba(0, 0, 0, 0);
  font-size: 20px;
}

.modal-login {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(65, 65, 65, .443);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
}

.modal-close {
  width: 30px;
  height: 30px;
  border: 1px solid #000;
  border-radius: 15px;
  position: absolute;
  top: 2%;
  right: 2%;
}

.login-form {
  max-width: 500px;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  display: flex;
  position: relative;
}

.login-form-input {
  width: 150px;
  box-sizing: border-box;
  border: 2px solid #dfdede;
  outline: none;
  padding: 2px 0 0 7px;
}

.login-form-input:hover, .login-form-input:focus {
  border-color: var(--active-link-color);
}

.login-form__login {
  margin-bottom: 5px;
}

.login-button {
  color: #1b1b1b;
  border: 4px var(--blue-color) solid;
  width: 100px;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, .494);
  margin-top: 10px;
  padding: 5px;
  transition: all .5s;
}

.login-button:hover {
  background-color: var(--active-link-color);
  color: azure;
  border: 4px solid #000;
}

.order-section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  transition: all;
